import * as React from 'react';
import ProviderSidebar from './sidebar';
import ProviderSidebarClose from './sidebarClose';
import Header from '../header';
import Alert from 'react-s-alert';
import 'react-s-alert/dist/s-alert-default.css';
import 'react-s-alert/dist/s-alert-css-effects/slide.css';

interface MyState {completedSessions:any};

class ProviderCompletedSession extends React.Component<MyState,MyProps> {
  constructor(props){
     super(props);
     this.state = {completedSession:[],deviceWidth:window.screen.width,listShow:true};
     if(this.state.deviceWidth>768){
       this.state.listShow = false;
     }else{
       this.state.listShow = true;
     }
     fetch('https://api.myjson.com/bins/101y9w', {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': sessionStorage.getItem('token'),
        }
      }).then((resp)=>{
        return resp.json()}).then((json)=>{
          this.setState({completedSession:json});
          Alert.warning('Test message warning!', {
           position: 'top-right'
         });
        }).catch(err => err);
     // this.completedSession = [
     //   {id:'1',title:'Business Studies',vendor:'Jane Doe',time_clocked:'00 : 30 : 34',multiplier:'1.5',tariff:'0.1',total_time:'1 : 30 : 55',start_time:'00 : 30 : 34',end_time:'00 : 30 : 34',end_date:'17-01-2018'},
     //   {id:'2',title:'2 States',vendor:'Hooq',time_clocked:'00 : 30 : 34',multiplier:'1.0',tariff:'0.1',total_time:'1 : 30 : 55',start_time:'00 : 30 : 34',end_time:'00 : 30 : 34',end_date:'17-01-2018'},
     //   {id:'3',title:'Hooq.com',vendor:'Hooq',time_clocked:'00 : 30 : 34',multiplier:'3.5',tariff:'0.1',total_time:'1 : 30 : 55',start_time:'00 : 30 : 34',end_time:'00 : 30 : 34',end_date:'17-01-2018'},
     //   {id:'4',title:'Batman V Superman',vendor:'Hotstar',time_clocked:'00 : 30 : 34',multiplier:'2.5',tariff:'0.1',total_time:'1 : 30 : 55',start_time:'00 : 30 : 34',end_time:'00 : 30 : 34',end_date:'17-01-2018'}
     // ];
     // this.state = {completedSession:this.completedSession};
     this.viewInfo = this.viewInfo.bind(this);
  }

  viewInfo(){
      this.props.history.push('/show-service-completed-sessions')
  }
render() {
return (
  <div>
      <Header />
        {/*<div className="container">
          <div className="row">
            <div className="col-sm-12">
              <nav className="navbar navbar-expand-lg navbar-light bg-light navbar-back" style={{padding:0}}>
                  <a className="navbar-brand" href="/"><img src="../assets/image/logo2.png" alt="logo" height={65} width={70} /></a>
                  <div className="w-100 text-center" id="navbarSupportedContent">
                    <ul className="navbar-nav mr-auto">

                    </ul>
                    <ul className="navbar-nav nav-right" id="navbarList">
                      <li className="nav-item" id="search">
                        <input type="text" name="search" placeholder="Search" className="form-control md-input" />
                      </li>
                    </ul>
                  </div>
                </nav>
            </div>
          </div>
        </div>*/}
        <div className="container">
          <div id="content">
          <div className="row margin-top50">
            <div className="col-sm-12">
              <label style={{fontSize: '20pt',textTransform: 'uppercase'}}>Completed Sessions</label>
            </div>
          </div>
            <div className="row margin-top50">
              <div className="col-sm-12" style={{}}>
              {this.state.listShow ? (
                <div>
                <table className="table table-box" style={{borderCollapse: 'separate', borderSpacing: '0 5px'}}>
                <thead style={{lineHeight:1}}>
                  <tr>
                    <th className="table-padding" style={{borderTop: 0, borderBottom: 0}}>Title</th>
                    <th className="table-padding" style={{borderTop: 0, borderBottom: 0}}>Total Time</th>
                  </tr>
                </thead>
                <tbody>
                {this.state.completedSession.map(obj => (
                  <tr className="table-card" style={{cursor:'pointer'}}  key={obj.id} onClick={this.viewInfo}>
                    <td className="table-padding">{obj.title.substring(0,9)}...</td>
                    <td className="table-padding">{obj.total_time}</td>
                  </tr>
                ))}
                </tbody>
                </table>
                </div>
              ) : (
                <div>
                <table className="table table-box" style={{borderCollapse: 'separate', borderSpacing: '0 5px'}}>
                <thead style={{lineHeight:1}}>
                  <tr>
                    <th className="table-padding" style={{borderTop: 0, borderBottom: 0}}>Title</th>
                    <th className="table-padding" style={{borderTop: 0, borderBottom: 0}}>Session Type</th>
                    <th className="table-padding" style={{borderTop: 0, borderBottom: 0}}>Start Time</th>
                    <th className="table-padding" style={{borderTop: 0, borderBottom: 0}}>End Date</th>
                    <th className="table-padding" style={{borderTop: 0, borderBottom: 0}}>End Time</th>
                    <th className="table-padding" style={{borderTop: 0, borderBottom: 0}}>Total Time</th>
                  </tr>
                </thead>
                <tbody>
                {this.state.completedSession.map(obj => (
                  <tr className="table-card" style={{cursor:'pointer'}}  key={obj.id} onClick={this.viewInfo}>
                    <td className="table-padding">{obj.title.substring(0,9)}...</td>
                    <td className="table-padding">{obj.vendor}</td>
                    <td className="table-padding">{obj.start_time}</td>
                    <td className="table-padding">{obj.end_date}</td>
                    <td className="table-padding">{obj.end_time}</td>
                    <td className="table-padding">{obj.total_time}</td>
                  </tr>
                ))}
                </tbody>
                </table>
                </div>
              )}
              </div>
            </div>
          </div>
          <ProviderSidebar />
        </div>
        <ProviderSidebarClose />
      </div>
    );
}
}
export default ProviderCompletedSession;



