import React, { Component } from 'react';
import Header from '../header';
import HomeSidebar from '../homeSidebar';
import HomeSidebarClose from '../homeSidebarClose';
import Alert from 'react-s-alert';
import 'react-s-alert/dist/s-alert-default.css';
import 'react-s-alert/dist/s-alert-css-effects/slide.css';
class UserLogin extends Component {
  constructor(props){
    super(props);
    this.login = this.login.bind(this);
  }
  login(){
    console.log('Login Works');
    let email = encodeURIComponent(this.refs.email.value);
    console.log(email);
    fetch('http://deltabee.com/directory-service/user/validate/'+email, {
              method: 'GET',
          }).then((res) => {
            if(res.status===200){
              this.props.history.push('/otp');
            }
          })
          .then((data) =>  console.log(data))
          .catch((err)=>console.log(err))
  }
render() {
return (
  <div>
  <Header />
  <Alert stack={true} timeout={3000} />

        <div className="container">
        <div id="content">
        <div className="row margin-top50">
          <div className="col-sm-12">
            <label style={{fontSize: '20pt',textTransform: 'uppercase'}}>Login for User</label>
          </div>
        </div>
          <div className="row margin-btm25  margin-top50">
          <div className="col-sm-3 col-offset-3"></div>
            <div className="col-sm-6">
              <p className="label-text fontbold" style={{fontWeight:700}}>Log In</p>
              <div className="card card-2 login-padd0" style={{padding:30}}>
                <div className="card-body">
                    <div className="row">
                      <div className="col-sm-12">
                        <div className="form-group">
                            <label className="label-text" style={{fontSize:'12pt'}}>Enter your registered phone number OR Email</label>
                            <input type="email" name="mobile_number" className="form-control md-input input-text" ref="email" style={{fontSize:'12pt !important'}}/>
                        </div>
                      </div>
                    </div>
                </div>
              </div>
              <div className="margin-top20">
                <p className="text-center"><a className="navbar-links color-black" onClick={this.login}>Login</a></p>
                <p className="text-center"><a className="navbar-links color-black"  href="/forgot-credentials">Forgot Credentials</a></p>
              </div>
            </div>
          </div>
        </div>
        <HomeSidebar />
        </div>
      <HomeSidebarClose />
      </div>
    );
}
}
export default UserLogin;



