import React, { Component } from 'react';

class Sidebar extends Component {
  openRightMenu() {
    document.getElementById("sidebar").style.display = "block";
    document.getElementById("closeicon").style.display = "block";
    document.getElementById("content").style.display = "none";
    //document.getElementById("navbarList").style.display = "none";
    document.getElementById("listIcon").style.display = "none";
  }
  closeRightMenu() {
    document.getElementById("sidebar").style.display = "none";
    document.getElementById("closeicon").style.display = "none";
    document.getElementById("content").style.display = "block";
    //document.getElementById("navbarList").style.display = "block";
    document.getElementById("listIcon").style.display = "block";
  }
render() {
return (
  <div>
    <div id="sidebar" className="animate-right" style={{display: 'none'}}>
      <div className="row" style={{marginTop: 20}}>
        <div className="col-sm-4" />
        <div className="col-sm-4">
        <ul className="navbar-nav mr-auto">
          <li className="nav-item" style={{paddingTop: 20}}>
            <a className="nav-link navbar-links" style={{color: '#000000'}}><span>Hi.. John, </span></a>
            <span style={{fontSize: '14pt',fontWeight: 700}}>your time balance is</span>
          </li>
          <li className="nav-item" style={{paddingTop: 20}}>
            {/*<span style={{fontWeight:'bold',fontSize:'14pt'}}>Total Balance (In Hours) : 10:04:00</span>*/}
            <div className="active-session-box">
              <label className="lineheight33" style={{fontSize:'16pt'}}>10:04:00</label>
            </div>
          </li>
          <li className="nav-item" style={{paddingTop: 30}}>
            <a className="nav-link navbar-links" style={{color: '#000000'}} href="/dashboard">Home</a>
          </li>
          <li className="nav-item" style={{paddingTop: 20}}>
            <a className="nav-link navbar-links" style={{color: '#000000'}} href="/vendor-active-sessions">Active Sessions</a>
          </li>
          <li className="nav-item" style={{paddingTop: 20}}>
            <a className="nav-link navbar-links" style={{color: '#000000'}} href="/scheduled-sessions">Scheduled Sessions</a>
          </li>
          <li className="nav-item" style={{paddingTop: 20}}>
            <a className="nav-link navbar-links" style={{color: '#000000'}} href="/completed-session">Completed Sessions</a>
          </li>
          <li className="nav-item" style={{paddingTop: 20}}>
            <a className="nav-link navbar-links" style={{color: '#000000'}} href="/recharge">Recharge</a>
          </li>
          <li className="nav-item" style={{paddingTop: 20}}>
            <a className="nav-link navbar-links" style={{color: '#000000'}} href="/user-setting">Setting</a>
          </li>
          <li className="nav-item" style={{paddingTop: 20}}>
            <a className="nav-link navbar-links" style={{color: '#000000'}}>Help</a>
          </li>
          <li className="nav-item" style={{paddingTop: 20}}>
            <a className="nav-link navbar-links" style={{color: '#000000'}} href="/">logout</a>
          </li>
        </ul>
        </div>
        <div className="col-sm-4" />
      </div>
    </div>
</div>
    );
}
}
export default Sidebar;



