import * as React from 'react';
import Header from '../header';
import HomeSidebar from '../homeSidebar';
import HomeSidebarClose from '../homeSidebarClose';
import Alert from 'react-s-alert';
import 'react-s-alert/dist/s-alert-default.css';
import 'react-s-alert/dist/s-alert-css-effects/slide.css';

class ServiceProviderSignup extends React.Component<MyState,MyProps> {
  constructor(props){
     super(props);
     this.state = {completedSession:[],companyName:'',email:'',country:'',bankAccountNumber:'',ifscCode:'',password:'',confirmPassword:''};
     fetch('https://api.myjson.com/bins/101y9w', {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': sessionStorage.getItem('token'),
        }
      }).then((resp)=>{
        return resp.json()}).then((json)=>{
          this.setState({completedSession:json});
        }).catch(err => err);
     this.register = this.register.bind(this);
  }

  register(){
    this.setState({companyName:this.refs.companyName.value,email:this.refs.email.value,bankAccountNumber:this.refs.bankAccountNumber.value,ifscCode:this.refs.ifscCode.value,password:this.refs.password.value,confirmPassword:this.refs.confirmPassword.value});
    if(this.refs.password.value===this.refs.confirmPassword.value){
      fetch('http://deltabee.com/directory-service/business-user', {
                method: 'POST',
                headers : {
                    'Content-Type': 'application/json'
                  },
                body:JSON.stringify({displayName: this.refs.companyName.value,email: this.refs.email.value,password:this.refs.confirmPassword.value,isActive:0,isDeleted:0})
            }).then((res) => {
              if(res.status===201){
                this.props.history.push('/service-provider-login');
              }
            })
            .then((data) =>  console.log(data))
            .catch((err)=>console.log(err))
    }else{
      Alert.warning('Pin Not Matched. Please Enter same pin', {
       position: 'top-right'
     });
    }
  }
render() {
return (
  <div>
    <Alert stack={{limit: 3}} />
    <Header />
    <div className="container">
    <div id="content">

       <div className="row margin-top50">
         <div className="col-sm-12">
           <label style={{fontSize: '20pt',textTransform: 'uppercase'}}>Register As a Service Provider</label>
         </div>
       </div>
         <div className="row margin-btm25  margin-top50">
          <div className="col-sm-3 col-offset-3"></div>
           <div className="col-md-6">
             <p className="font15 fontbold">Service Provider Sign Up</p>
             <div className="card card-2" style={{padding:30}}>
               <div className="card-body">
                 <div className="row">
                   <div className="col-md-12 margin-btm25">
                      <div className="form-group">
                          <label className="label-text" style={{fontSize:'12pt'}}>Enter Individual or Company name</label>
                          <input type="text" name="company_name" className="form-control md-input input-text" ref="companyName"/>
                      </div>
                   </div>
                   <div className="col-md-12 margin-btm25">
                      <div className="form-group">
                          <label className="label-text" style={{fontSize:'12pt'}}>Enter Email</label>
                          <input type="email" name="email" className="form-control md-input input-text" ref="email" />
                      </div>
                   </div>
                   <div className="col-md-12 margin-btm25">
                      <div className="form-group">
                          <label className="label-text" style={{fontSize:'12pt'}}>Enter Bank account number for outbound transactions</label>
                          <input type="text" name="bank_account" className="form-control md-input input-text" ref="bankAccountNumber" />
                      </div>
                   </div>
                   <div className="col-md-12 margin-btm25">
                      <div className="form-group">
                        <label className="label-text" style={{fontSize:'12pt'}}>Enter Bank account IFSC code</label>
                        <input type="text" name="ifsc" className="form-control md-input input-text" ref="ifscCode"/>
                      </div>
                   </div>
                   <div className="col-md-12 margin-btm25">
                      <div className="form-group">
                        <label className="label-text" style={{fontSize:'12pt'}}>Enter password</label>
                        <input type="password" name="password" className="form-control md-input input-text" ref="password" />
                      </div>
                   </div>
                   <div className="col-md-12 margin-btm25">
                      <div className="form-group">
                        <label className="label-text" style={{fontSize:'12pt'}}>Re  Enter password</label>
                        <input type="password" name="confirmpassword" className="form-control md-input input-text" ref="confirmPassword" />
                      </div>
                   </div>
                 </div>
               </div>
             </div>
             <div className="margin-top20">
               <p className="text-center"><a className="navbar-links color-black" onClick={this.register}>Complete Registration</a></p>
             </div>
           </div>
         </div>
       </div>
       <HomeSidebar />
       </div>
       <HomeSidebarClose />
       </div>
    );
}
}
export default ServiceProviderSignup;



