import React, { Component } from 'react';
import Header from './header';

class Information extends Component {
  openRightMenu() {
    document.getElementById("sidebar").style.display = "block";
    document.getElementById("closeicon").style.display = "block";
    document.getElementById("content").style.display = "none";
    // document.getElementById("navbarList").style.display = "none";
    document.getElementById("listIcon").style.display = "none";
  }
  closeRightMenu() {
    document.getElementById("sidebar").style.display = "none";
    document.getElementById("closeicon").style.display = "none";
    document.getElementById("content").style.display = "block";
    // document.getElementById("navbarList").style.display = "block";
    document.getElementById("listIcon").style.display = "block";
  }
render() {
return (
  <div>
        <Header />
        {/*<div className="container">
          <div className="row">
            <div className="col-sm-12">
              <nav className="navbar navbar-expand-lg navbar-light bg-light navbar-back" style={{padding:0}}>
                  <a className="navbar-brand" href="/"><img src="../assets/image/logo2.png" alt="logo" height={65} width={70} /></a>
                  <div className="w-100 text-center" id="navbarSupportedContent">
                    <ul className="navbar-nav mr-auto">

                    </ul>
                  </div>
                </nav>
            </div>
          </div>
        </div>*/}
        <div id="content">
        <div className="container">
        <div className="row margin-top50">
          <div className="col-sm-12">
            <label style={{fontSize: '20pt',textTransform: 'uppercase'}}>Information</label>
          </div>
        </div>
          <div className="row margin-top40">
            <div className="col-sm-12">
            <label className="label-text" style={{fontWeight:700}}>About Xantt</label>
              <div className="card">
                <div className="card-body">
                  <div>An idea that was always floating around for</div>
                </div>
              </div>
            </div>
          </div>
          <div className="row margin-top40" >
            <div className="col-sm-12">
            <label className="label-text" style={{fontWeight:700}}>Contact Us</label>
              <div className="card">
                <div className="card-body">
                  <div>Ops_hq@xantt.xyz</div>
                </div>
              </div>
            </div>
          </div>
          <div className="row margin-top40" >
            <div className="col-sm-12">
            <label className="label-text" style={{fontWeight:700}}>Management</label>
              <div className="card">
                <div className="card-body">
                  <div>Ops_hq@xantt.xyz</div>
                </div>
              </div>
            </div>
          </div>
        </div>
        </div>
        <div id="sidebar" className="animate-right" style={{display: 'none'}}>
          <div className="row" style={{marginTop: 20}}>
            <div className="col-sm-4 col-offset-4" />
            <div className="col-sm-4">
            <ul className="navbar-nav mr-auto">
              <li className="nav-item" style={{paddingTop: 20}}>
                <a className="nav-link navbar-links" style={{color: '#000000'}} href="/user-login">Login For Users</a>
              </li>
              <li className="nav-item" style={{paddingTop: 20}}>
                <a className="nav-link navbar-links" style={{color: '#000000'}} href="/user-registration">Register As a user</a>
              </li>
              <li className="nav-item" style={{paddingTop: 20}}>
                <a className="nav-link navbar-links" style={{color: '#000000'}} href="/service-provider-login">Login for service providers</a>
              </li>
              <li className="nav-item" style={{paddingTop: 20}}>
                <a className="nav-link navbar-links" style={{color: '#000000'}} href="/service-provider-signup">Register as a service providers</a>
              </li>
              <li className="nav-item" style={{paddingTop: 20}}>
                <a className="nav-link navbar-links" style={{color: '#000000'}} href="/information">Information</a>
              </li>
            </ul>
            </div>
          </div>
        </div>
        <div className="closeMenu" id="listIcon" >
          <a  onClick={this.openRightMenu}><img src="../assets/image/ham4.png" alt="hambergure" height={50} width={50} /></a>
        </div>
        <div className="closeMenu" id="closeicon" style={{display: 'none'}}>
          <a onClick={this.closeRightMenu}><i className="fa fa-times fa-3x"></i></a>
        </div>
      </div>
    );
}
}
export default Information;



