import React, { Component } from 'react';
// import DateTimePicker from 'react-datetime-picker';
// import Globalize from 'globalize';
// import globalizeLocalizer from 'react-widgets-globalize'
// import { DateTimePicker } from 'react-widgets'
import ProviderSidebar from './sidebar';
import ProviderSidebarClose from './sidebarClose';
import Header from '../header';

class PublishExhibit extends Component {
  constructor(props){
    super(props);
    this.saveExhibit = this.saveExhibit.bind(this);
  }

  saveExhibit(){
    console.log(this.refs.startDate.value);
    console.log(this.refs.endDate.value);

    let startDate = this.refs.startDate.value;
    startDate = startDate.split('T');
    console.log(startDate);

    let endDate = this.refs.endDate.value;
    endDate = endDate.split('T');
    console.log(endDate);

    fetch('http://deltabee.com/exhibit-service/exhibit', {
              method: 'POST',
              headers : {
                  'Content-Type': 'application/json'
                },
              body:JSON.stringify({dateOfEnd:endDate[0],dateOfStart:startDate[0],startTime:startDate[1],endTime:endDate[1],exhibitUrl:this.refs.exhibitUrl.value,imageUrl:'',multiplier:this.refs.multiplier.value,title:this.refs.exhibitName.value})
          }).then((res) => {
            if(res.status===201){
              this.props.history.push('/exhibits');
            }
          })
          .then((data) =>  console.log(data))
          .catch((err)=>console.log(err))
  }



render() {
return (
  <div>
        <Header />
        {/*<div className="container">
          <div className="row">
            <div className="col-sm-12">
              <nav className="navbar navbar-expand-lg navbar-light bg-light navbar-back" style={{padding:0}}>
                  <a className="navbar-brand" href="/"><img src="../assets/image/logo2.png" alt="logo" height={65} width={70} /></a>
                  <div className="w-100 text-center" id="navbarSupportedContent">
                    <ul className="navbar-nav mr-auto">

                    </ul>
                    <ul className="navbar-nav nav-right" id="navbarList">

                    </ul>
                  </div>
                </nav>
            </div>
          </div>
        </div>*/}
        <div className="container">
          <div id="content">
          <div className="row margin-top50">
            <div className="col-sm-12">
              <label style={{fontSize: '20pt',textTransform: 'uppercase'}}>Actions</label>
            </div>
          </div>
            <div className="row margin-btm25  margin-top50">
            <div className="col-sm-3 col-offset-3"></div>
              <div className="col-sm-6">
                <p className="label-text fontbold" style={{fontWeight:700}}>Publish an exhibit</p>
                <div className="card card-2">
                  <div className="card-body">
                    <div className="row margin-btm25">
                      <div className="col-sm-12">
                        <div className="form-group">
                          <label className='label-text' style={{fontSize:'12pt'}}>Paste URL</label>
                          <input type="text" name="url" className="form-control md-input input-text" ref="exhibitUrl"/>
                        </div>
                      </div>
                    </div>
                    <div className="row margin-btm25">
                      <div className="col-sm-12">
                        <div className="form-group">
                          <label className='label-text' style={{fontSize:'12pt'}}>Name of Exhibit</label>
                          <input type="text" name="exhibit_name" className="form-control md-input input-text" ref="exhibitName" />
                        </div>
                      </div>
                    </div>
                    <div className="row margin-btm25">
                      <div className="col-sm-12">
                        <div className="form-group">
                          <label className='label-text' style={{fontSize:'12pt'}}>Enter Multiplier</label>
                          <input type="text" name="multiplier" className="form-control md-input input-text" ref="multiplier"/>
                        </div>
                      </div>
                    </div>
                    <div className="row margin-btm25">
                      <div className="col-sm-12">
                        <div className="form-group">
                          <label className='label-text' style={{fontSize:'12pt'}}>Set Start Date and time</label>
                          <input type="datetime-local" name="start_date" className="form-control md-input input-text" ref="startDate"/>
                        </div>
                      </div>
                    </div>
                    <div className="row margin-btm25">
                      <div className="col-sm-12">
                        <div className="form-group">
                          <label className='label-text' style={{fontSize:'12pt'}}>Set End Date and time</label>
                          <input type="datetime-local" name="end_date" className="form-control md-input input-text" ref="endDate"/>
                        </div>
                      </div>
                    </div>
                    <div className="row margin-btm25">
                      <div className="col-sm-12">
                        <div className="form-group">
                          <label className='label-text' style={{fontSize:'12pt'}}>Set a thumbnail / display image</label>
                          <input type="file" name="thumbnail" className="form-control md-input input-text" ref="imageUrl"/>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className="row">
              <div className="col-sm-3 col-offset-3"></div>
              <div className="col-sm-6">
                <div className="margin-top20">
                  <p className="text-center"><a className="navbar-links color-black" onClick={this.saveExhibit}>Publish</a></p>
                </div>
              </div>
            </div>

            <div className="row margin-btm25 margin-top40">
            <div className="col-sm-3 col-offset-3"></div>
              <div className="col-sm-6">
                <p className="label-text" style={{fontWeight:700}}>API Integration, coming soon....</p>
                {/*<div className="card card-2">
                  <div className="card-body">
                    <div className="row margin-btm25">
                      <div className="col-sm-4">
                        <label className='label-text'>Oauth Client ID</label>
                      </div>
                      <div className="col-sm-8">
                        <input type="text" name="client_id" className="form-control md-input input-text" />
                      </div>
                    </div>
                    <div className="row margin-btm25" >
                      <div className="col-sm-4">
                        <label className='label-text'>Oauth Security key</label>
                      </div>
                      <div className="col-sm-8">
                        <input type="text" name="security_key" className="form-control md-input input-text" />
                      </div>
                    </div>
                    <div className="row margin-btm25">
                      <div className="col-sm-4">
                        <label className='label-text'>Status On / Off</label>
                      </div>
                      <div className="col-sm-8">
                        <input type="text" name="status" className="form-control md-input input-text" />
                      </div>
                    </div>
                  </div>
                </div>*/}
              </div>
            </div>
          </div>
          <ProviderSidebar />
        </div>
        <ProviderSidebarClose />
      </div>
    );
}
}
export default PublishExhibit;



