import * as React from 'react';
import ProviderSidebar from './sidebar';
import ProviderSidebarClose from './sidebarClose';
import Header from '../header';

import Alert from 'react-s-alert';
import 'react-s-alert/dist/s-alert-default.css';
import 'react-s-alert/dist/s-alert-css-effects/slide.css';

interface MyState {activeSessions:any};

class ShowSeriveActiveSessions extends React.Component<MyState,MyProps> {
  constructor(props){
    super(props);
    this.state = {activeSessions:[]};
    fetch('http://deltabee.com/exhibit-service/exhibit/1', {
       method: 'GET',
       headers: {
         'Content-Type': 'application/json',
         'Authorization': sessionStorage.getItem('token'),
       }
     }).then((resp)=>{
       return resp.json()}).then((json)=>{
         this.setState({activeSessions:json});
         Alert.warning('Test message warning!', {
          position: 'top-right'
        });
       }).catch(err => err);
  }
render() {
return (
  <div>
  <Header />
  {/*<div className="container">
    <div className="row">
      <div className="col-sm-12">
        <nav className="navbar navbar-expand-lg navbar-light bg-light navbar-back">
            <a className="navbar-brand" href="/"><img src="../assets/image/logo2.png" alt="logo" height={65} width={70} /></a>
            <div className="w-100 text-center" id="navbarSupportedContent">
            <ul className="navbar-nav nav-right" id="navbarList">
              <li className="nav-item" id="search">
                <input type="text" name="search" placeholder="Search" className="form-control md-input" />
              </li>
            </ul>
            </div>
          </nav>
      </div>
    </div>
  </div>*/}
  <div className="container">
    <div id="content">
    {this.state.activeSessions.map(obj => (
      <div className="row" style={{marginTop: 30, marginBottom:30}} key={obj.id}>
        {/*<div className="col-sm-4">
          <div className="card card-box text-white bg-warning mb-3">
            <div className="card-body">
              <p className="card-text content-header" >{obj.user_name}</p>
              <label className="exhibit-title">{obj.title}</label>
              <label className="exhibit-count">1.5x</label>
            </div>
          </div>
          <div className='text-center margin-btm10'><a className="navbar-links" style={{color: '#000000',marginLeft:0,textTransform: 'uppecase'}}>Stop Session</a></div>
        </div>*/}
        <div className="col-sm-2 col-offset-2">

        </div>
        <div className="col-sm-8">
          <div className="card card-2">
          <div className="card-box text-white bg-warning mb-3" style={{height:200}}>

          </div>
            <div className="card-body">
              <div className="row">

                <div className="col-sm-4 col-5">
                  <div><label className="label-text">User name : </label></div>
                </div>
                <div className="col-sm-7 col-7">
                  <div className="info">{obj.user_name}</div>
                </div>
              </div>
              <div className="row">

                <div className="col-sm-4 col-5">
                  <div><label className="label-text">Session Type : </label></div>
                </div>
                <div className="col-sm-7 col-7">
                  <div className="info">{obj.session_type}</div>
                </div>
              </div>
              <div className="row">

                <div className="col-sm-4 col-5">
                  <div><label className="label-text">Exhibit Title : </label></div>
                </div>
                <div className="col-sm-7 col-7">
                  <div className="info">{obj.title}</div>
                </div>
              </div>
              <div className="row">

                <div className="col-sm-4 col-5">
                  <div><label className="label-text">Start Date : </label></div>
                </div>
                <div className="col-sm-7 col-7">
                  <div className="info">{obj.start_date}</div>
                </div>
              </div>
              <div className="row">

                <div className="col-sm-4 col-5">
                  <div><label className="label-text">Start Time : </label></div>
                </div>
                <div className="col-sm-7 col-7">
                  <div className="info">{obj.start_time}</div>
                </div>
              </div>
              <div className="row">

                <div className="col-sm-4 col-5">
                  <div><label className="label-text">Time Clocked : </label></div>
                </div>
                <div className="col-sm-7 col-7">
                  <div className="info">{obj.time_clock}</div>
                </div>
              </div>
            </div>
          </div>
          <div className="row" style={{marginTop:50}}>
            <div className="col-sm-2"></div>
            <div className="col-sm-8">
            <div className='text-center margin-btm10'><a className="navbar-links" style={{color: '#000000',marginLeft:0}}>stop session</a></div>
              <div className='text-center margin-btm10' style={{marginTop:20}}><a href="/active-sessions" className="navbar-links"><img src="../assets/image/cross-345.png" alt="logo" height={30} width={30} /></a></div>
            </div>
            <div className="col-sm-2"></div>
          </div>
        </div>
      </div>
    ))}
    </div>
    <ProviderSidebar />
  </div>
  <ProviderSidebarClose />
</div>
    );
}
}
export default ShowSeriveActiveSessions;



