import * as React from 'react';
import ProviderSidebar from './sidebar';
import ProviderSidebarClose from './sidebarClose';
import Header from '../header';
import Alert from 'react-s-alert';
import 'react-s-alert/dist/s-alert-default.css';
import 'react-s-alert/dist/s-alert-css-effects/slide.css';

interface MyState {completedSessions:any};

class ShowServiceCompletedSession extends React.Component<MyState,MyProps> {
  constructor(props){
     super(props);
     this.state = {completedSession:[]};
     fetch('https://api.myjson.com/bins/101y9w', {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': sessionStorage.getItem('token'),
        }
      }).then((resp)=>{
        return resp.json()}).then((json)=>{
          this.setState({completedSession:json});
          Alert.warning('Test message warning!', {
           position: 'top-right'
         });
        }).catch(err => err);
  }
render() {
return (
  <div>
    <Header />
  {/*<div className="container">
    <div className="row">
      <div className="col-sm-12">
        <nav className="navbar navbar-expand-lg navbar-light bg-light navbar-back">
            <a className="navbar-brand" href="/"><img src="../assets/image/logo2.png" alt="logo" height={65} width={70} /></a>
            <div className="w-100 text-center" id="navbarSupportedContent">
            <ul className="navbar-nav nav-right" id="navbarList">
              <li className="nav-item" id="search">
                <input type="text" name="search" placeholder="Search" className="form-control md-input" />
              </li>
            </ul>
            </div>
          </nav>
      </div>
    </div>
  </div>*/}
  <div className="container">
    <div id="content">

    {this.state.completedSession.map(obj => (
      <div className="row" style={{marginTop: 30,marginBottom:30}} key={obj.id}>
        {/*<div className="col-sm-4">
          <div className="card card-box text-white bg-warning mb-3">
            <div className="card-body">
              <p className="card-text content-header" >{obj.vendor}</p>
              <label className="exhibit-title">{obj.title}</label>
              <label className="exhibit-count">{obj.multiplier}x</label>
            </div>
          </div>
        </div>*/}
        <div className="col-sm-2 col-offset-2"></div>
        <div className="col-sm-8">
          <div className="card card-2">
          <div className="card-box text-white bg-warning mb-3" style={{height:200}}>

          </div>
            <div className="card-body">
              <div className="row">

                <div className="col-sm-4 col-5">
                  <div><label className="label-text">Title : </label></div>
                </div>
                <div className="col-sm-7 col-7">
                  <div className="info">{obj.title}</div>
                </div>
              </div>
              <div className="row">

                <div className="col-sm-4 col-5">
                  <div><label className="label-text">Session Type : </label></div>
                </div>
                <div className="col-sm-7 col-7">
                  <div className="info">{obj.vendor}</div>
                </div>
              </div>
              <div className="row">

                <div className="col-sm-4 col-5">
                  <div><label className="label-text">Start time : </label></div>
                </div>
                <div className="col-sm-7 col-7">
                  <div className="info">{obj.start_time}</div>
                </div>
              </div>
              <div className="row">

                <div className="col-sm-4 col-5">
                  <div><label className="label-text">Total Time : </label></div>
                </div>
                <div className="col-sm-7 col-7">
                  <div className="info">{obj.total_time}</div>
                </div>
              </div>
              <div className="row">

                <div className="col-sm-4 col-5">
                  <div><label className="label-text">End Date : </label></div>
                </div>
                <div className="col-sm-7 col-7">
                <div className="info">{obj.end_date}</div>
                </div>
              </div>
              <div className="row">

                <div className="col-sm-4 col-5">
                  <div><label className="label-text">End Time : </label></div>
                </div>
                <div className="col-sm-7 col-7">
                <div className="info">{obj.end_time}</div>
                </div>
              </div>
            </div>
          </div>
          <div className="row" style={{marginTop:50}}>
            <div className="col-sm-2"></div>
            <div className="col-sm-8">
              <div className='text-center margin-btm10' style={{marginTop:20}}><a href="/provider-completed-session" className="navbar-links"><img src="../assets/image/cross-345.png" alt="logo" height={30} width={30} /></a></div>
            </div>
            <div className="col-sm-2"></div>
          </div>
        </div>
      </div>
    ))}
    </div>
    <ProviderSidebar />
  </div>
  <ProviderSidebarClose />
</div>
    );
}
}
export default ShowServiceCompletedSession;



