import React, { Component } from 'react';
import Header from '../header';
import Alert from 'react-s-alert';
import * as $ from 'jquery';

import 'react-s-alert/dist/s-alert-default.css';
import 'react-s-alert/dist/s-alert-css-effects/slide.css';
class Otp extends Component {
  constructor(){
      super();
      //this.value1
      this.state = {value: '',value1:'',value2:'',value3:''};
      this.onChange1 = this.onChange1.bind(this);
      this.onChange2 = this.onChange2.bind(this);
      this.onChange3 = this.onChange3.bind(this);
      this.onChange4 = this.onChange4.bind(this);

   }

   onChange1(e){
      const re = /^[0-9\b]+$/;
      if (e.target.value === '' || re.test(e.target.value)) {
         this.setState({value: e.target.value});
         $('#otp2').focus();
      }
   }
   onChange2(e){
      const re = /^[0-9\b]+$/;
      if (e.target.value === '' || re.test(e.target.value)) {
         this.setState({value1: e.target.value})
         $('#otp3').focus();
      }
   }
   onChange3(e){
      const re = /^[0-9\b]+$/;
      if (e.target.value === '' || re.test(e.target.value)) {
         this.setState({value2: e.target.value})
         $('#otp4').focus();
      }
   }
   onChange4(e){
      const re = /^[0-9\b]+$/;
      if (e.target.value === '' || re.test(e.target.value)) {
         this.setState({value3: e.target.value})
      }
   }
render() {
return (
  <div>
      <Header />
        <Alert stack={true} timeout={3000} />
        {/*<div className="container">

          <div className="row">
            <div className="col-sm-12">
              <nav className="navbar navbar-expand-lg navbar-light bg-light navbar-back" style={{padding:0}}>
                  <a className="navbar-brand" href="/"><img src="../assets/image/logo2.png" alt="logo" height={65} width={70} /></a>
                  <div className="w-100 text-center" id="navbarSupportedContent">
                    <ul className="navbar-nav mr-auto">

                    </ul>
                    <ul className="navbar-nav nav-right" id="navbarList" style={{visibility:'hidden'}}>
                      <li className="nav-item" id="search">
                        <a className="nav-link navbar-links label-text" href="/user-registration">Register As User</a>
                      </li>
                    </ul>
                  </div>
                </nav>
            </div>
          </div>
        </div>*/}

        <div className="container">
          <div className="row">
            <div className="col-xs-12 col-md-6 panel-box">
              <p className="label-text" style={{}}>Hi janedoe@gmail.com</p>
              <div className="card card-2 padd-top75">
                <div className="card-body">
                  <div className="row">
                    <div className="col-sm-5">
                      <label className="label-text">Enter your PIN</label>
                    </div>
                    <div className="col-sm-7">
                      <div className="row">
                        <div className="col-sm-3 col-3"><input type="password" id="otp1" name="otp1" className="text-center padd-left0 padd-right0 orm-control md-input input-text" minLength={1} maxLength={1} value ={this.state.value} onChange={this.onChange1}/></div>
                        <div className="col-sm-3 col-3"><input type="password" id="otp2" name="otp2" className="text-center padd-left0 padd-right0 orm-control md-input input-text" minLength={1} maxLength={1} value ={this.state.value1} onChange={this.onChange2}/></div>
                        <div className="col-sm-3 col-3"><input type="password" id="otp3" name="otp3" className="text-center padd-left0 padd-right0 orm-control md-input input-text" minLength={1} maxLength={1} value ={this.state.value2} onChange={this.onChange3}/></div>
                        <div className="col-sm-3 col-3"><input type="password" id="otp4" name="otp4" className="text-center padd-left0 padd-right0 orm-control md-input input-text" minLength={1} maxLength={1} value ={this.state.value3} onChange={this.onChange4}/></div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div className="margin-top20 text-center">
                <a className="navbar-links color-black"  href="/dashboard">Verify</a>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
}
}
export default Otp;



