import * as React from 'react';
import ProviderSidebar from './sidebar';
import ProviderSidebarClose from './sidebarClose';
import Header from '../header';

interface MyState {activeSessions:any};

class ActiveSessions extends React.Component<MyState,MyProps> {
  constructor(props){
    super(props);
    this.state = {activeSessions:[],deviceWidth:window.screen.width,listShow:true};
    if(this.state.deviceWidth>768){
      this.state.listShow = false;
    }else{
      this.state.listShow = true;
    }
    fetch('http://deltabee.com/verification-service/session/exhibit/1?page=1', {
       method: 'GET',
       headers: {
         'Content-Type': 'application/json',
         'Authorization': sessionStorage.getItem('token'),
       }
     }).then((resp)=>{
       return resp.json()}).then((json)=>{
         this.setState({activeSessions:json});
       }).catch(err => err);
    this.viewInfo = this.viewInfo.bind(this);
    // this.activeSessions = [
    //   {id:1,user_name:'akcghtll',session_type:'Scheduled',title:'Accounts 101',start_date:'12-07-2018',start_time:'12:34:43',time_clock:'00:23:45'},
    //   {id:2,user_name:'prtsrnk',session_type:'Scheduled',title:'Accounts 101',start_date:'12-07-2018',start_time:'12:34:43',time_clock:'00:21:09'},
    //   {id:3,user_name:'llkiops',session_type:'Scheduled',title:'Accounts 101',start_date:'12-07-2018',start_time:'12:34:43',time_clock:'00:00:55'},
    //   {id:4,user_name:'awsllop',session_type:'Scheduled',title:'Accounts 101',start_date:'12-07-2018',start_time:'12:34:43',time_clock:'00:12:19'},
    //   {id:5,user_name:'asmwkk',session_type:'Scheduled',title:'Accounts 101',start_date:'12-07-2018',start_time:'12:34:43',time_clock:'00:14:03'},
    //   {id:6,user_name:'asmwkk',session_type:'Scheduled',title:'Accounts 101',start_date:'12-07-2018',start_time:'12:34:43',time_clock:'00:14:03'}
    // ];
    // this.state = {activeSessions:this.activeSessions};
  }

  viewInfo(){
      this.props.history.push('/show-service-active-sessions');
  }
render() {
return (
  <div>
      <Header />
        {/*<div className="container">
          <div className="row">
            <div className="col-sm-12">
              <nav className="navbar navbar-expand-lg navbar-light bg-light navbar-back" style={{padding:0}}>
                  <a className="navbar-brand" href="/"><img src="../assets/image/logo2.png" alt="logo" height={65} width={70} /></a>
                  <div className="w-100 text-center" id="navbarSupportedContent">
                    <ul className="navbar-nav mr-auto">

                    </ul>
                    <ul className="navbar-nav nav-right" id="navbarList">
                      <li className="nav-item" id="search">
                        <input type="text" name="search" placeholder="Search" className="form-control md-input" />
                      </li>
                    </ul>
                  </div>
                </nav>
            </div>
          </div>
        </div>*/}

        <div className="container">
          <div id="content">
          <div className="row margin-top50">
            <div className="col-sm-12">
              <label style={{fontSize: '20pt',textTransform: 'uppercase'}}>Active Sessions</label>
            </div>
          </div>
            <div className="row margin-top50">
              <div className="col-sm-5">
                <label className="label-text">
                total active sessions
                  <div className="active-session-box">
                    <label className="lineheight33">5</label>
                  </div>
                </label>
              </div>
            </div>
            <div className="row" style={{marginTop: 10}}>
              <div className="col-sm-12">
              {this.state.listShow ? (
                <div>
                <table className="table table-box" style={{borderCollapse: 'separate', borderSpacing: '0 5px'}}>
                <thead style={{lineHeight:1}}>
                  <tr>
                    <th className="table-padding bor-btm0 bor-top0">User name</th>
                    <th className="table-padding bor-btm0 bor-top0">Time Clocked</th>
                  </tr>
                </thead>
                <tbody>
                  {this.state.activeSessions.map(obj => (
                    <tr className="table-card" style={{cursor:'pointer'}}  key={obj.id} onClick={this.viewInfo}>
                      <td className="table-padding">{obj.user_name.substring(0,9)}...</td>
                      <td className="table-padding">{obj.time_clock}</td>
                    </tr>
                  ))}
                </tbody>
                </table>
                </div>
              ) : (
                <div>
                <table className="table table-box" style={{borderCollapse: 'separate', borderSpacing: '0 5px'}}>
                <thead style={{lineHeight:1}}>
                  <tr>
                    <th className="table-padding bor-btm0 bor-top0">User name</th>
                    <th className="table-padding bor-btm0 bor-top0">Session Type</th>
                    <th className="table-padding bor-btm0 bor-top0">Exhibit Title</th>
                    <th className="table-padding bor-btm0 bor-top0">Start Date</th>
                    <th className="table-padding bor-btm0 bor-top0">Start Time</th>
                    <th className="table-padding bor-btm0 bor-top0">Time Clocked</th>
                  </tr>
                </thead>
                <tbody>
                {this.state.activeSessions.map(obj => (
                  <tr className="table-card" style={{cursor:'pointer'}}  key={obj.id} onClick={this.viewInfo}>
                    <td className="table-padding">{obj.user_name.substring(0,9)}...</td>
                    <td className="table-padding">{obj.session_type}</td>
                    <td className="table-padding">{obj.title}</td>
                    <td className="table-padding">{obj.start_date}</td>
                    <td className="table-padding">{obj.start_time}</td>
                    <td className="table-padding">{obj.time_clock}</td>
                  </tr>
                ))}
                </tbody>
                </table>
                </div>
              )}
              </div>
            </div>
          </div>
          <ProviderSidebar />
        </div>
        <ProviderSidebarClose />

      </div>
    );
}
}
export default ActiveSessions;



