import React, { Component } from 'react';
// import { DateTimePicker } from 'react-widgets'
import Home from './User/home';
import ActiveSessions from './ServiceProvider/activeSessions';
import AddScheduledSession from './User/addScheduledSession';
import CompletedSession from './User/completedSession';
import Dashboard from './home/dashboard';
import Exhibits from './ServiceProvider/exhibits';
import Information from './information';
import Otp from './User/otp';
import PublishExhibit from './ServiceProvider/publishExhibit';
import Recharge from './User/recharge';
import Redeem from './ServiceProvider/redeem';
import ScheduledSession from './User/scheduledSession';
import ServiceProviderLogin from './ServiceProvider/serviceProviderLogin';
import ServiceProviderRegistration from './ServiceProvider/seviceProviderRegistration';
import ServiceProviderSignup from './ServiceProvider/serviceProviderSignup';
import Setting from './ServiceProvider/setting';
import UserLogin from './User/userLogin';
import UserSetting from './User/userSetting';
import VendorActiveSessions from './User/vendorActiveSessions';
import ForgotCredentials from './ServiceProvider/forgotCredentials';
import ProviderCompletedSession from './ServiceProvider/completedSession';
import UserRegistration from './User/userRegistration';
import ShowActiveSession from './User/showActiveSession';
import ShowScheduledSession from './User/showScheduledSession';
import ShowCompletedSession from './User/showCompletedSession';
import ShowRecharges from './User/showRecharges';
import ShowSeriveActiveSessions from './ServiceProvider/showActiveSessions';
import ShowServiceCompletedSession from './ServiceProvider/showCompletedSession';
import ShowExhibits from './ServiceProvider/showExhibits';
import ShowReedem from './ServiceProvider/showReedem';

import { BrowserRouter as Router } from 'react-router-dom';
import Route from 'react-router-dom/Route';

class App extends Component {
  render() {
    return (
      <Router>
        <div className="App">
            <Route path="/" exact component={Dashboard}/>
            <Route path="/active-sessions" exact component={ActiveSessions}/>
            <Route path="/add-scheduled-session" exact component={AddScheduledSession}/>
            <Route path="/completed-session" exact component={CompletedSession}/>
            <Route path="/dashboard" exact component={Home}/>
            <Route path="/exhibits" exact component={Exhibits}/>
            <Route path="/information" exact component={Information}/>
            <Route path="/otp" exact component={Otp}/>
            <Route path="/publish-exhibit" exact component={PublishExhibit}/>
            <Route path="/recharge" exact component={Recharge}/>
            <Route path="/redeem" exact component={Redeem}/>
            <Route path="/scheduled-sessions" exact component={ScheduledSession}/>
            <Route path="/service-provider-login" exact component={ServiceProviderLogin}/>
            <Route path="/service-provider-registration" exact component={ServiceProviderRegistration}/>
            <Route path="/service-provider-signup" exact component={ServiceProviderSignup}/>
            <Route path="/setting" exact component={Setting}/>
            <Route path="/user-login" exact component={UserLogin}/>
            <Route path="/user-setting" exact component={UserSetting}/>
            <Route path="/vendor-active-sessions" exact component={VendorActiveSessions}/>
            <Route path="/forgot-credentials" exact component={ForgotCredentials}/>
            <Route path="/provider-completed-session" exact component={ProviderCompletedSession}/>
            <Route path="/user-registration" exact component={UserRegistration}/>
            <Route path="/show-active-session" exact component={ShowActiveSession}/>
            <Route path="/show-scheduled-session" exact component={ShowScheduledSession}/>
            <Route path="/show-completed-session" exact component={ShowCompletedSession}/>
            <Route path="/show-recharges" exact component={ShowRecharges}/>
            <Route path="/show-service-active-sessions" exact component={ShowSeriveActiveSessions}/>
            <Route path="/show-service-completed-sessions" exact component={ShowServiceCompletedSession}/>
            <Route path="/show-exhibits" exact component={ShowExhibits}/>
            <Route path="/show-reedem" exact component={ShowReedem}/>
        </div>
      </Router>
    );
    }
  }
export default App;



