import React, { Component } from 'react';

class ServiceProviderRegistration extends Component {
render() {
return (
  <div>
        <nav className="navbar navbar-expand-lg navbar-light navbar-back bg-light">
        <a className="navbar-brand" href="/"><img src="../assets/image/logo2.png" alt="logo" height={65} width={70} /></a>

          <div className="w-100 text-center" id="navbarSupportedContent">
            <ul className="navbar-nav mr-auto">
            </ul>
            <ul className="navbar-nav">
              <li className="nav-item">
                <a className="nav-link navbar-links" style={{color: '#000000'}} href="/service-provider-login">Log In</a>
              </li>
            </ul>
          </div>
        </nav>
        <div className="container" style={{}}>
          <div className="row">
            <div className="col-sm-2" />
            <div className="col-xs-12 col-sm-8">
              <label className="label-text">Service Provider Log In</label>
            </div>
            <div className="col-sm-2" />
          </div>
          <div className="row">
            <div className="col-sm-2">
            </div>
            <div className="col-xs-12 col-sm-8">
              <div className="card card-2 login-padd0" style={{}}>
                <div className="card-body">
                  <div className="row margin-btm25">
                    <div className="col-xs-12 col-sm-5">
                      <label className="label-text">Enter Individual or Company Name</label>
                    </div>
                    <div className="col-xs-12 col-sm-7">
                      <input type="text" name="company_name" className="form-control md-input input-text" />
                    </div>
                  </div>
                  <div className="row margin-btm25" style={{}}>
                    <div className="col-xs-12 col-sm-5">
                      <label className="label-text">Enter Email ID</label>
                    </div>
                    <div className="col-xs-12 col-sm-7">
                      <input type="text" name="email" className="form-control md-input input-text input-text" />
                    </div>
                  </div>
                  <div className="row margin-btm25" style={{}}>
                    <div className="col-xs-12 col-sm-5">
                      <label className="label-text">Set Password</label>
                    </div>
                    <div className="col-xs-12 col-sm-7">
                      <input type="password" name="password" className="form-control md-input input-text" />
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className="col-sm-2" />
          </div>
          <div className="row" style={{marginTop:30}}>
            <div className="col-sm-4" />
            <div className="col-xs-12 col-sm-4">
              <p style={{textAlign: 'center'}}><a className="navbar-links" style={{color: '#000000'}} href="/service-provider-signup">Proceed</a></p>
            </div>
            <div className="col-sm-4" />
          </div>
        </div>
      </div>
    );
}
}
export default ServiceProviderRegistration;



