import * as React from 'react';
import Sidebar from './sidebar';
import SidebarClose from './sidebarClose';
import Header from '../header';
import Alert from 'react-s-alert';
import 'react-s-alert/dist/s-alert-default.css';
import 'react-s-alert/dist/s-alert-css-effects/slide.css';

interface MyState {completedSession:any};

class CompletedSession extends React.Component<MyProps,MyState> {
  constructor(props){
     super(props);
     this.state = {completedSession:[],deviceWidth:window.screen.width,listShow:true};
     if(this.state.deviceWidth>768){
       this.state.listShow = false;
     }else{
       this.state.listShow = true;
     }

     fetch('https://api.myjson.com/bins/1cwwxw', {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': sessionStorage.getItem('token'),
        }
      }).then((resp)=>{
        return resp.json()}).then((json)=>{
          this.setState({completedSession:json});
          Alert.warning('Test message warning!', {
           position: 'top-right'
         });
        }).catch(err => err);
     this.viewInfo = this.viewInfo.bind(this);

  }

  viewInfo(){
      this.props.history.push('/show-completed-session')
  }
render() {
return (
  <div>
      <Header />
        {/*<div className="container">

          <div className="row">
            <div className="col-sm-12">
              <nav className="navbar navbar-expand-lg navbar-light bg-light navbar-back" style={{padding:0}}>
                  <a className="navbar-brand" href="/"><img src="../assets/image/logo2.png" alt="logo" height={65} width={70} /></a>
                  <div className="w-100 text-center" id="navbarSupportedContent">
                    <ul className="navbar-nav mr-auto">

                    </ul>
                    <ul className="navbar-nav nav-right" id="navbarList">
                      <li className="nav-item" id="search">
                        <input type="text" name="search" placeholder="Search" className="form-control md-input" />
                      </li>
                    </ul>
                  </div>
                </nav>
            </div>
          </div>
        </div>*/}
        <div className="container">
          <div id="content">
          <div className="row margin-top50">
            <div className="col-sm-12">
              <label style={{fontSize: '20pt',textTransform: 'uppercase'}}>Completed Sessions</label>
            </div>
          </div>
            <div className="row margin-top50">
              <div className="col-sm-12" >
              {this.state.listShow ? (
                <div>
                <table className="table table-box" style={{borderCollapse: 'separate', borderSpacing: '0 5px'}}>
                <thead style={{lineHeight:1}}>
                <tr>
                  <th className="table-padding bor-btm0 bor-top0" >Title</th>
                  <th className="table-padding bor-btm0 bor-top0" >Total Time</th>
                </tr>
                </thead>
                <tbody>
                {this.state.completedSession.map(obj => (
                  <tr className="table-card" style={{cursor:'pointer'}}  key={obj.id} onClick={this.viewInfo}>
                    <td className="table-padding">{obj.title.substring(0,9)}...</td>
                    <td className="table-padding">{obj.total_time}</td>
                  </tr>
                ))}
                </tbody>
                </table>
                </div>
              ) : (
                <div>
                <table className="table table-box" style={{borderCollapse: 'separate', borderSpacing: '0 5px'}}>
                <thead style={{lineHeight:1}}>
                  <tr>
                    <th className="table-padding bor-btm0 bor-top0" >Title</th>
                    <th className="table-padding bor-btm0 bor-top0" >Exhibitor</th>
                    <th className="table-padding bor-btm0 bor-top0" >Multiplier</th>
                    <th className="table-padding bor-btm0 bor-top0" >Time Clocked</th>
                    {/*<th className="table-padding bor-btm0 bor-top0" >Tariff</th>*/}
                    <th className="table-padding bor-btm0 bor-top0" >Total Time</th>
                  </tr>
                </thead>
                <tbody>
                {this.state.completedSession.map(obj => (
                  <tr className="table-card" style={{cursor:'pointer'}}  key={obj.id} onClick={this.viewInfo}>
                    <td className="table-padding">{obj.title.substring(0,9)}...</td>
                    <td className="table-padding">{obj.vendor}</td>
                    <td className="table-padding">{obj.multiplier}</td>
                    <td className="table-padding">{obj.start_time}</td>

                    {/*<td className="table-padding">{obj.tariff}</td>*/}
                    <td className="table-padding">{obj.total_time}</td>
                  </tr>
                ))}
                </tbody>
                </table>
                </div>
              )}
              </div>
            </div>
            <div className="row" style={{display: 'none'}}>
              <div className="col-sm-3" />
              <div className="col-sm-6">
                <ul className="pagination">
                  <li className="page-item"><a className="page-link">Previous</a></li>
                  <li className="page-item active"><a className="page-link">1</a></li>
                  <li className="page-item"><a className="page-link">2</a></li>
                  <li className="page-item"><a className="page-link">3</a></li>
                  <li className="page-item"><a className="page-link">4</a></li>
                  <li className="page-item"><a className="page-link">5</a></li>
                  <li className="page-item"><a className="page-link">Next</a></li>
                </ul>
              </div>
              <div className="col-sm-3" />
            </div>
          </div>
          <Sidebar />
        </div>
        <SidebarClose />
      </div>
    );
}
}
export default CompletedSession;



