import React, { Component } from 'react';
import ProviderSidebar from './sidebar';
import ProviderSidebarClose from './sidebarClose';
import Header from '../header';

class Setting extends Component {

render() {
return (
  <div>
        <Header />
        {/*<div className="container">
          <div className="row">
            <div className="col-sm-12">
              <nav className="navbar navbar-expand-lg navbar-light bg-light navbar-back" style={{padding:0}}>
                  <a className="navbar-brand" href="/"><img src="../assets/image/logo2.png" alt="logo" height={65} width={70} /></a>
                  <div className="w-100 text-center" id="navbarSupportedContent">
                    <ul className="navbar-nav mr-auto">

                    </ul>
                    <ul className="navbar-nav nav-right" id="navbarList" style={{visibility:'hidden'}}>
                      <li className="nav-item" id="search">
                        <input type="text" name="search" placeholder="Search" className="form-control md-input" />
                      </li>
                    </ul>
                  </div>
                </nav>
            </div>
          </div>
        </div>*/}
        <div className="container">
          <div id="content">
          <div className="row margin-top50">
            <div className="col-sm-12">
              <label style={{fontSize: '20pt',textTransform: 'uppercase'}}>setting</label>
            </div>
          </div>
            <div className="row margin-top50">
            <div className="col-sm-3 col-6 margin-btm30 black-container">
              <label className="label-text margin-rgt20">
              Live Exhibit
                <div className="active-session-box">
                  <label className="lineheight33">INR 5 / hr</label>
                </div>
              </label>
            </div>
            <div className="col-sm-3 col-6 margin-btm30 black-container">
              <label className="label-text margin-rgt20">
              Tarrif rate
                <div className="active-session-box">
                  <label className="lineheight33">1%</label>
                </div>
              </label>
            </div>
            <div className="col-sm-3 col-6 margin-btm30 black-container">
              <label className="label-text margin-rgt20">
              Transaction Cost
                <div className="active-session-box">
                  <label className="lineheight33">15%</label>
                </div>
              </label>
            </div>
            </div>
            <div className="row">
              <div className="col-xs-12 col-sm-8 offset-sm-2">
                <div className="card card-2">
                  <div className="card-body">
                    <div className="row margin-btm25">
                      <div className="col-sm-12">
                        <div className="form-group">
                        <label className='label-text' style={{fontSize:'12pt'}}>Individual or Company name</label>
                        <input type="text" name="company_name" className="form-control md-input input-text" defaultValue="Jane Doe" />
                        </div>
                      </div>
                    </div>
                    <div className="row margin-btm25" >
                      <div className="col-sm-12">
                        <div className="form-group">
                        <label className='label-text' style={{fontSize:'12pt'}}>Login Email</label>
                        <input type="text" name="email" className="form-control md-input input-text" defaultValue="abc@gmail.com" />
                        </div>
                      </div>
                    </div>
                    <div className="row margin-btm25" >
                      <div className="col-sm-12">
                        <div className="form-group">
                        <label className='label-text' style={{fontSize:'12pt'}}>Password</label>
                        <input type="password" name="password" className="form-control md-input input-text" defaultValue={1234} />
                        </div>
                      </div>
                    </div>
                    <div className="row margin-btm25" >
                      <div className="col-sm-12">
                        <div className="form-group">
                        <label className='label-text' style={{fontSize:'12pt'}}>Bank Account Number</label>
                        <input type="text" name="bank_account" className="form-control md-input input-text" defaultValue="Bank Account Number" />
                        </div>
                      </div>
                    </div>
                    <div className="row margin-btm25" >
                      <div className="col-sm-12">
                        <div className="form-group">
                        <label className='label-text' style={{fontSize:'12pt'}}>Bank IFSC CODE</label>
                        <input type="text" name="ifsc" className="form-control md-input input-text" defaultValue="IFSC CODE" />
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="margin-top20">
                  <p className="text-center"><a className="navbar-links color-black"  href="/active-sessions">SAVE CHANGES</a></p>
                </div>
              </div>
            </div>
          </div>
          <ProviderSidebar />
        </div>
        <ProviderSidebarClose />
      </div>
    );
}
}
export default Setting;



