import * as React from 'react';
import ProviderSidebar from './sidebar';
import ProviderSidebarClose from './sidebarClose';
import Header from '../header';
import Alert from 'react-s-alert';
import 'react-s-alert/dist/s-alert-default.css';
import 'react-s-alert/dist/s-alert-css-effects/slide.css';

interface MyState {exhibits:any};

class Exhibits extends React.Component<MyState,MyProps> {
  constructor(props){
    super(props);
    this.state = {exhibits:[],deviceWidth:window.screen.width,listShow:true};
    this.viewInfo = this.viewInfo.bind(this);
    console.log(this.state);
    if(this.state.deviceWidth>768){
      this.state.listShow = false;
    }else{
      this.state.listShow = true;
    }
    // this.exhibits = [
    //   {id:'1',exhibit_name:'Accounts 101',status:'Scheduled',multiplier:1.5,start_date:'24-July-2018',start_time:'18:00:00',end_date:'24-July-2018',end_time:'19:00:00',added_users:'6',tarrif:'1x'}
    // ];
    fetch('http://deltabee.com/exhibit-service/exhibit?page=1', {
       method: 'GET',
       headers: {
         'Content-Type': 'application/json',
         'Authorization': sessionStorage.getItem('token'),
       }
     }).then((resp)=>{
       return resp.json()}).then((json)=>{
         this.setState({exhibits:json});
         Alert.warning('Test message warning!', {
          position: 'top-right'
        });
       }).catch(err => err);
    //this.state = {exhibits:this.exhibits};
  }

  viewInfo(){
      this.props.history.push('/show-exhibits')
  }
render() {
  // let exhibitsData = [
  //   {id:'1',exhibit_name:'Accounts 101',sessio_type:'Scheduled',multiplier:1.5,start_date:'24-July-2018',start_time:'18:00:00',end_date:'24-July-2018',end_time:'19:00:00',added_users:'6'}
  // ];

return (
  <div>
        <Header />
        {/*<div className="container">
          <div className="row">
            <div className="col-sm-12">
              <nav className="navbar navbar-expand-lg navbar-light bg-light navbar-back" style={{padding:0}}>
                  <a className="navbar-brand" href="/"><img src="../assets/image/logo2.png" alt="logo" height={65} width={70} /></a>
                  <div className="w-100 text-center" id="navbarSupportedContent">
                    <ul className="navbar-nav mr-auto">

                    </ul>
                    <ul className="navbar-nav nav-right" id="navbarList">
                      <li className="nav-item" id="search">
                        <input type="text" name="search" placeholder="Search" className="form-control md-input" />
                      </li>
                    </ul>
                  </div>
                </nav>
            </div>
          </div>
        </div>*/}
        <div className="container">
          <div id="content">
          <div className="row margin-top50">
            <div className="col-sm-12">
              <label style={{fontSize: '20pt',textTransform: 'uppercase'}}>Exhibits</label>
            </div>
          </div>
            <div className="row margin-top50">
              <div className="col-sm-11 margin-btm30">
                <label className="label-text margin-rgt20">
                  <div className="active-session-box">
                    <label className="lineheight33">1</label>
                  </div>
                </label>
                <label className="label-text">
                  Live Exhibit
                </label>
              </div>
            </div>
            <div className="row">
              <div className="col-sm-12">
                {/*<table className="table table-box" style={{borderCollapse: 'separate', borderSpacing: '0 5px'}}>*/}
                  {this.state.listShow ? (
                    <div>
                    <table className="table table-box" style={{borderCollapse: 'separate', borderSpacing: '0 5px'}}>
                    <thead style={{lineHeight:1}}>
                      <tr>
                        <th className="table-padding bor-btm0 bor-top0" >Title</th>
                        <th className="table-padding bor-btm0 bor-top0" >Active Sessions</th>
                      </tr>
                    </thead>
                    <tbody>
                    {this.state.exhibits.map(obj => (
                      <tr className="table-card" style={{cursor:'pointer'}}  key={obj.id} onClick={this.viewInfo}>
                        <td className="table-padding">{obj.exhibit_name.substring(0,9)}...</td>
                        <td className="table-padding">{obj.added_users}</td>
                      </tr>
                    ))}
                    </tbody>
                    </table>
                    </div>
                  ) : (
                    <div>
                    <table className="table table-box" style={{borderCollapse: 'separate', borderSpacing: '0 5px'}}>
                    <thead style={{lineHeight:1}}>
                      <tr>
                        <th className="table-padding bor-btm0 bor-top0" >Title</th>
                        <th className="table-padding bor-btm0 bor-top0" >Multiplier</th>
                        <th className="table-padding bor-btm0 bor-top0" >Type</th>
                        <th className="table-padding bor-btm0 bor-top0" >Start Date</th>
                        <th className="table-padding bor-btm0 bor-top0" >Start Time</th>
                        {/*<th className="table-padding bor-btm0 bor-top0" >Xantt Tariff</th>*/}
                        <th className="table-padding bor-btm0 bor-top0" >Active Sessions</th>
                        {/*<th className="table-padding bor-btm0 bor-top0">Action</th>*/}
                      </tr>
                    </thead>
                    <tbody>
                    {this.state.exhibits.map(obj => (
                      <tr className="table-card" style={{cursor:'pointer'}}  key={obj.id} onClick={this.viewInfo}>
                        <td className="table-padding">{obj.exhibit_name.substring(0,9)}...</td>
                        <td className="table-padding">{obj.multiplier}</td>
                        <td className="table-padding">{obj.status}</td>
                        <td className="table-padding">{obj.start_date}</td>
                        <td className="table-padding">{obj.start_time}</td>
                        {/*<td className="table-padding">{obj.tarrif}</td>*/}
                        <td className="table-padding">{obj.added_users}</td>
                        {/*<td className="table-padding"><a href="completed-session" className="navbar-links color-black" style={{fontSize:'12pt'}}>Delete Exhibit</a></td>*/}
                      </tr>
                    ))}
                    </tbody>
                    </table>
                    </div>
                  )}
                  {/*<thead style={{lineHeight:1}}>
                    <tr>
                      <th className="table-padding bor-btm0 bor-top0" >Title</th>
                      <th className="table-padding bor-btm0 bor-top0" >Multiplier</th>
                      <th className="table-padding bor-btm0 bor-top0" >Type</th>
                      <th className="table-padding bor-btm0 bor-top0" >Start Date</th>
                      <th className="table-padding bor-btm0 bor-top0" >Start Time</th>
                      {/*<th className="table-padding bor-btm0 bor-top0" >Xantt Tariff</th>
                      <th className="table-padding bor-btm0 bor-top0" >Active Sessions</th>
                      <th className="table-padding bor-btm0 bor-top0">Action</th>
                    </tr>
                  </thead>
                  <tbody>
                  {this.state.exhibits.map(obj => (
                    <tr className="table-card" style={{cursor:'pointer'}}  key={obj.id} onClick={this.viewInfo}>
                      <td className="table-padding">{obj.exhibit_name}</td>
                      <td className="table-padding">{obj.multiplier}</td>
                      <td className="table-padding">{obj.status}</td>
                      <td className="table-padding">{obj.start_date}</td>
                      <td className="table-padding">{obj.start_time}</td>
                      {/*<td className="table-padding">{obj.tarrif}</td>
                      <td className="table-padding">{obj.added_users}</td>
                      <td className="table-padding"><a href="completed-session" className="navbar-links color-black" style={{fontSize:'12pt'}}>Delete Exhibit</a></td>
                    </tr>
                  ))}
                  </tbody>*/}
                {/*</table>*/}
              </div>

            </div>
          </div>
          <ProviderSidebar />
        </div>
        <ProviderSidebarClose />
      </div>
    );
}
}
export default Exhibits;



