import React, { Component } from 'react';
import Header from '../header';
import HomeSidebar from '../homeSidebar';
import HomeSidebarClose from '../homeSidebarClose';
import {FormErrors} from './reactForm';

class ServiceProviderLogin extends Component {
  constructor(props){
    super(props);
    this.state = {email:'',password:'',formErrors: {email: '', password: ''},emailValid: false,passwordValid: false,formValid: false,errorShow:false};
    this.login = this.login.bind(this);
  }
  login(){
    console.log('Login Works');
    let email = encodeURIComponent(this.refs.email.value);
    console.log(email);
    fetch('http://deltabee.com/directory-service/business-user/validate/'+email, {
              method: 'GET',
          }).then((res) => {
            if(res.status===200){
              this.props.history.push('/active-sessions');
            }
          })
          .then((data) =>  console.log(data))
          .catch((err)=>console.log(err))
  }

  handleUserInput (e) {
    const name = e.target.name;
    const value = e.target.value;
    console.log(name);
    console.log(value);
    this.setState({[name]: value},
                () => { this.validateField(name, value) });
  }

  validateField(fieldName, value) {
  let fieldValidationErrors = this.state.formErrors;
  let emailValid = this.state.emailValid;
  let passwordValid = this.state.passwordValid;

  switch(fieldName) {
    case 'email':
      emailValid = value.match(/^([\w.%+-]+)@([\w-]+\.)+([\w]{2,})$/i);
      fieldValidationErrors.email = emailValid ? '' : ' is invalid';
      this.state.errorShow = emailValid ? false : true;
      break;
    case 'password':
      passwordValid = value.length >= 6;
      fieldValidationErrors.password = passwordValid ? '': ' is too short';
      this.state.errorShow = passwordValid ? false : true;
      break;
    default:
      break;
  }
  this.setState({formErrors: fieldValidationErrors,
                  emailValid: emailValid,
                  passwordValid: passwordValid
                }, this.validateForm);
}

validateForm() {
  this.setState({formValid: this.state.emailValid && this.state.passwordValid});
}
render() {
return (
  <div>
        <Header />
        {/*<div className="container">
          <div className="row">
            <div className="col-sm-12">
              <nav className="navbar navbar-expand-lg navbar-light bg-light navbar-back" style={{padding:0}}>
                  <a className="navbar-brand" href="/"><img src="../assets/image/logo2.png" alt="logo" height={65} width={70} /></a>
                  <div className="w-100 text-center" id="navbarSupportedContent">
                    <ul className="navbar-nav mr-auto">

                    </ul>
                    <ul className="navbar-nav nav-right" id="navbarList">
                      <li className="nav-item" id="search">
                        <a className="nav-link navbar-links label-text" href="/service-provider-signup">Register As Service Provider</a>
                      </li>
                    </ul>
                  </div>
                </nav>
            </div>
          </div>
        </div>*/}
        <div className="container">
        <div id="content">
        <div className="row margin-top50">
          <div className="col-sm-12">
            <label style={{fontSize: '20pt',textTransform: 'uppercase'}}>Login for Service Provider</label>
          </div>
        </div>
        <div className="row">
        <div className="col-sm-3 col-offset-3"></div>
          <div className="col-sm-6">
            {this.state.errorShow ? (
              <div className='alert alert-danger'>
               <FormErrors formErrors={this.state.formErrors} />
              </div>
            ) :  (
              <div className='alert alert-success' style={{'display':'none'}}>
               <FormErrors formErrors={this.state.formErrors} />
              </div>
            )}
          </div>
        </div>
          <div className="row margin-btm25  margin-top50">
          <div className="col-sm-3 col-offset-3"></div>
            <div className="col-md-6">
              <p className="label-text fontbold">Service Provider Login</p>
              <div className="card card-2 login-padd0" style={{padding:30}}>
                <div className="card-body">
                <form className="demoForm">
                  <div className="row">
                    <div className="col-sm-12">
                      <div className="form-group">
                          <label className='label-text' style={{fontSize:'12pt'}}>Enter Email ID</label>
                          <input type="email" name="email" className="form-control md-input input-text" ref="email" onChange={(event) => this.handleUserInput(event)}/>
                      </div>
                    </div>
                    <div className="col-sm-12">
                      <div className="form-group">
                          <label className='label-text' style={{fontSize:'12pt'}}>Enter Password</label>
                            <input type="password" name="password" className="form-control md-input input-text" ref="password" onChange={(event) => this.handleUserInput(event)}/>
                      </div>
                    </div>
                  </div>
                  </form>
                </div>
              </div>
              <div className="margin-top20">
                <p className="text-center"><a className="navbar-links color-black" disabled={!this.state.formValid} onClick={this.login}>Login</a></p>
                <p className="text-center"><a className="navbar-links color-black"  href="/forgot-credentials">Forgot Credentials</a></p>
              </div>
            </div>
          </div>
        </div>
        <HomeSidebar />
        </div>
        <HomeSidebarClose />
      </div>
    );
}
}
export default ServiceProviderLogin;



