import * as React from 'react';
import ProviderSidebar from './sidebar';
import ProviderSidebarClose from './sidebarClose';
import Header from '../header';
import Alert from 'react-s-alert';
import 'react-s-alert/dist/s-alert-default.css';
import 'react-s-alert/dist/s-alert-css-effects/slide.css';

interface MyState {exhibits:any};

class ShowExhibits extends React.Component<MyState,MyProps> {
  constructor(props){
    super(props);
    this.state = {exhibits:[]};
    // this.exhibits = [
    //   {id:'1',exhibit_name:'Accounts 101',status:'Scheduled',multiplier:1.5,start_date:'24-July-2018',start_time:'18:00:00',end_date:'24-July-2018',end_time:'19:00:00',added_users:'6',tarrif:'1x'}
    // ];
    fetch('http://deltabee.com/exhibit-service/exhibit/1', {
       method: 'GET',
       headers: {
         'Content-Type': 'application/json',
         'Authorization': sessionStorage.getItem('token'),
       }
     }).then((resp)=>{
       return resp.json()}).then((json)=>{
         this.setState({exhibits:json});
       }).catch(err => err);
    //this.state = {exhibits:this.exhibits};
  }

render() {
return (
  <div>
  <Header />
  {/*<div className="container">
    <div className="row">
      <div className="col-sm-12">
        <nav className="navbar navbar-expand-lg navbar-light bg-light navbar-back">
            <a className="navbar-brand" href="/"><img src="../assets/image/logo2.png" alt="logo" height={65} width={70} /></a>
            <div className="w-100 text-center" id="navbarSupportedContent">
            <ul className="navbar-nav nav-right" id="navbarList">
              <li className="nav-item" id="search">
                <input type="text" name="search" placeholder="Search" className="form-control md-input" />
              </li>
            </ul>
            </div>
          </nav>
      </div>
    </div>
  </div>*/}
  <div className="container">
    <div id="content">
      {this.state.exhibits.map(obj => (
        <div className="row" style={{marginTop: 30,marginBottom:30}} key={obj.id}>
          {/*<div className="col-sm-4">
            <div className="card card-box text-white bg-warning mb-3">
              <div className="card-body">
                <p className="card-text content-header" >{obj.vendor}</p>
                <label className="exhibit-title">{obj.exhibit_name}</label>
                <label className="exhibit-count">{obj.multiplier}x</label>
              </div>
            </div>
          </div>*/}
          <div className="col-sm-2 col-offset-2"></div>
          <div className="col-sm-8">
            <div className="card card-2">
            <div className="card-box text-white bg-warning mb-3" style={{height:200}}>

            </div>
              <div className="card-body">
                <div className="row">

                  <div className="col-sm-4 col-5">
                    <div><label className="label-text">Title : </label></div>
                  </div>
                  <div className="col-sm-7 col-7">
                    <div className="info">{obj.exhibit_name}</div>
                  </div>
                </div>
                <div className="row">
                  <div className="col-sm-4 col-5">
                    <div><label className="label-text">Multiplier : </label></div>
                  </div>
                  <div className="col-sm-7 col-7">
                    <div className="info">{obj.multiplier}</div>
                  </div>
                </div>
                <div className="row">
                  <div className="col-sm-4 col-5">
                    <div><label className="label-text">Status : </label></div>
                  </div>
                  <div className="col-sm-7 col-7">
                    <div className="info">{obj.status}</div>
                  </div>
                </div>
                <div className="row">

                  <div className="col-sm-4 col-5">
                    <div><label className="label-text">Start Time : </label></div>
                  </div>
                  <div className="col-sm-7 col-7">
                    <div className="info">{obj.start_time}</div>
                  </div>
                </div>
                <div className="row">

                  <div className="col-sm-4 col-5">
                    <div><label className="label-text">Start Date : </label></div>
                  </div>
                  <div className="col-sm-7 col-7">
                  <div className="info">{obj.start_date}</div>
                  </div>
                </div>
                <div className="row">

                  <div className="col-sm-4 col-5">
                    <div><label className="label-text">Xantt Tariff : </label></div>
                  </div>
                  <div className="col-sm-7 col-7">
                  <div className="info">{obj.tarrif}</div>
                  </div>
                </div>
                <div className="row">

                  <div className="col-sm-4 col-5">
                    <div><label className="label-text">End Date : </label></div>
                  </div>
                  <div className="col-sm-7 col-7">
                  <div className="info">{obj.end_date}</div>
                  </div>
                </div>
                <div className="row">

                  <div className="col-sm-4 col-5">
                    <div><label className="label-text">End Time : </label></div>
                  </div>
                  <div className="col-sm-7 col-7">
                  <div className="info">{obj.end_time}</div>
                  </div>
                </div>
                <div className="row">

                  <div className="col-sm-4 col-5">
                    <div><label className="label-text">Active Sessions : </label></div>
                  </div>
                  <div className="col-sm-7 col-7">
                  <div className="info">{obj.added_users}</div>
                  </div>
                </div>
                <div className="row">

                  <div className="col-sm-4 col-5">
                    <div><label className="label-text">URL : </label></div>
                  </div>
                  <div className="col-sm-7 col-7">
                  <div className="info">{obj.url}</div>
                  </div>
                </div>
              </div>
            </div>
            <div className="row" style={{marginTop:50}}>
              <div className="col-sm-2"></div>
              <div className="col-sm-8">
              <div className='text-center margin-btm10'><a className="navbar-links" style={{color: '#000000',marginLeft:0}}>delete exhibit</a></div>
                <div className='text-center margin-btm10' style={{marginTop:20}}><a href="/exhibits" className="navbar-links"><img src="../assets/image/cross-345.png" alt="logo" height={30} width={30} /></a></div>
              </div>
              <div className="col-sm-2"></div>
            </div>
          </div>
        </div>
      ))}

    </div>
    <ProviderSidebar />
  </div>
  <ProviderSidebarClose />
</div>
    );
}
}
export default ShowExhibits;



