import React, { Component } from 'react';

class Header extends Component {
render() {
return (
  <div>
  <div className="container">
    <div className="row">
      <div className="col-sm-12">
        <input type="text" className="form-control" id="searchBox" style={{marginTop:30}}/><i className="fa fa-search icon-alignment" style={{top: 50,right: 20}}></i>
      </div>
    </div>
  </div>
  </div>
    );
}
}
export default Header;



