import * as React from 'react';
import ProviderSidebar from './sidebar';
import ProviderSidebarClose from './sidebarClose';
import Header from '../header';
import Alert from 'react-s-alert';
import 'react-s-alert/dist/s-alert-default.css';
import 'react-s-alert/dist/s-alert-css-effects/slide.css';

interface MyState {redeem:any}
class ShowReedem extends React.Component<MyState,MyProps> {
  constructor(props){
    super(props);
    this.state = {redeem:[]};

    fetch('https://api.myjson.com/bins/1c2wp0', {
       method: 'GET',
       headers: {
         'Content-Type': 'application/json',
         'Authorization': sessionStorage.getItem('token'),
       }
     }).then((resp)=>{
       return resp.json()}).then((json)=>{
         this.setState({redeem:json});
         Alert.warning('Test message warning!', {
          position: 'top-right'
        });
       }).catch(err => err);
     }
render() {
return (
  <div>
  <Header />
  {/*<div className="container">
    <div className="row">
      <div className="col-sm-12">
        <nav className="navbar navbar-expand-lg navbar-light bg-light navbar-back">
            <a className="navbar-brand" href="/"><img src="../assets/image/logo2.png" alt="logo" height={65} width={70} /></a>
            <div className="w-100 text-center" id="navbarSupportedContent">
            <ul className="navbar-nav nav-right" id="navbarList">
              <li className="nav-item" id="search">
                <input type="text" name="search" placeholder="Search" className="form-control md-input" />
              </li>
            </ul>
            </div>
          </nav>
      </div>
    </div>
  </div>*/}
  <div className="container">
    <div id="content">
    {this.state.redeem.map(obj => (
      <div className="row" style={{marginTop: 30, marginBottom:30}} key={obj.id}>
      <div className="col-sm-2 col-sm-2"></div>
        <div className="col-sm-8">
          <div className="card card-2">
            <div className="card-body">
              <div className="row">

                <div className="col-sm-4 col-5">
                  <div><label className="label-text">Date : </label></div>
                </div>
                <div className="col-sm-7 col-7">
                  <div className="info">{obj.date}</div>
                </div>
              </div>
              <div className="row">

                <div className="col-sm-4 col-5">
                  <div><label className="label-text">Transaction Cost : </label></div>
                </div>
                <div className="col-sm-7 col-7">
                  <div className="info">{obj.tarrif}</div>
                </div>
              </div>
              <div className="row">

                <div className="col-sm-4 col-5">
                  <div><label className="label-text">Status : </label></div>
                </div>
                <div className="col-sm-7 col-7">
                  <div className="info">{obj.status}</div>
                </div>
              </div>
              <div className="row">

                <div className="col-sm-4 col-5">
                  <div><label className="label-text">Currency : </label></div>
                </div>
                <div className="col-sm-7 col-7">
                  <div className="info">{obj.currency}</div>
                </div>
              </div>
              <div className="row">

                <div className="col-sm-4 col-5">
                  <div><label className="label-text">Amount : </label></div>
                </div>
                <div className="col-sm-7 col-7">
                  <div className="info">{obj.amount}</div>
                </div>
              </div>
              <div className="row">

                <div className="col-sm-4 col-5">
                  <div><label className="label-text">Time Deducted : </label></div>
                </div>
                <div className="col-sm-7 col-7">
                  <div className="info">{obj.deducted_time}</div>
                </div>
              </div>
              <div className="row">

                <div className="col-sm-4 col-5">
                  <div><label className="label-text">Exchange Reference ID : </label></div>
                </div>
                <div className="col-sm-7 col-7">
                  <div className="info">ABC1151545</div>
                </div>
              </div>
            </div>
          </div>
          <div className="row" style={{marginTop:50}}>
            <div className="col-sm-2"></div>
            <div className="col-sm-8">
              <div className='text-center margin-btm10' style={{marginTop:20}}><a href="/redeem" className="navbar-links"><img src="../assets/image/cross-345.png" alt="logo" height={30} width={30} /></a></div>
            </div>
            <div className="col-sm-2"></div>
          </div>
        </div>
      </div>
    ))}
    </div>
    <ProviderSidebar />
  </div>
  <ProviderSidebarClose />
</div>
    );
}
}
export default ShowReedem;



