import * as React from 'react';
import Sidebar from './sidebar';
import SidebarClose from './sidebarClose';
import Header from '../header';
import Alert from 'react-s-alert';
import 'react-s-alert/dist/s-alert-default.css';
import 'react-s-alert/dist/s-alert-css-effects/slide.css';
import * as $ from 'jquery';

interface MyState {recharges:any,hours:string,amount:string,order_number:string};

class Recharge extends React.Component<MyState,MyProps> {
  constructor(props){
     super(props);
     this.state = {recharges:[],hours:'',amount:'',order_number:'',deviceWidth:window.screen.width,listShow:true};
     if(this.state.deviceWidth>768){
       this.state.listShow = false;
     }else{
       this.state.listShow = true;
     }
     fetch('https://api.myjson.com/bins/l1ttw', {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': sessionStorage.getItem('token'),
        }
      }).then((resp)=>{
        return resp.json()}).then((json)=>{
          this.setState({recharges:json});
          Alert.warning('Test message warning!', {
           position: 'top-right'
         });
        }).catch(err => err);

     this.viewInfo = this.viewInfo.bind(this);
     this.onHoursChange = this.onHoursChange.bind(this);
     this.onAmountChange = this.onAmountChange.bind(this);
     this.pay = this.pay.bind(this);
     this.close = this.close.bind(this);
  }

  viewInfo(){
    this.props.history.push('/show-recharges')
  }
  onHoursChange(e){
    e.preventDefault();
    this.setState({hours:e.target.value,amount:(e.target.value)*5*1.18});
  }
  onAmountChange(e){
    e.preventDefault();
    this.setState({amount:e.target.value});
  }
  close(e){
    e.preventDefault();
    $('#myModal').hide();
  }
  pay(e){
    e.preventDefault();
    let options = {
      "key": "rzp_test_GZ03qX0OLbOSyN",
      "amount": (this.state.amount)*100, // 2000 paise = INR 20, amount in paisa
      "name": "Xantt",
      "description": "Wallet Recharge",
      "image": "../assets/image/logo2.png",
      "handler": function (response){
        //alert('Payment Id is : '+response.razorpay_payment_id);
        //this.state = {order_number:response.razorpay_payment_id};
        $('#order_number').append(response.razorpay_payment_id);
        $('#myModal').show();
      },
      "prefill": {
        "name": "Harshil Mathur",
        "email": "harshil@razorpay.com"
      },
      "notes": {
        "address": "Hello World"
      },
      "theme": {
        "color": "#F37254"
      }
    };

    let rzp = new window.Razorpay(options);
    rzp.open();
  }
render() {
return (
  <div>
        <Header />
        {/*<div className="container">
          <div className="row">
            <div className="col-sm-12">
              <nav className="navbar navbar-expand-lg navbar-light bg-light navbar-back" style={{padding:0}}>
                  <a className="navbar-brand" href="/"><img src="../assets/image/logo2.png" alt="logo" height={65} width={70} /></a>
                  <div className="w-100 text-center" id="navbarSupportedContent">
                    <ul className="navbar-nav mr-auto">

                    </ul>
                    <ul className="navbar-nav nav-right" id="navbarList">
                      <li className="nav-item" id="search">
                        <input type="text" name="search" placeholder="Search" className="form-control md-input" />
                      </li>
                    </ul>
                  </div>
                </nav>
            </div>
          </div>
        </div>*/}
        <div className="container">
          <div id="content">
          <div className="row margin-top50">
            <div className="col-sm-12">
              <label style={{fontSize: '20pt',textTransform: 'uppercase'}}>Recharge</label>
            </div>
          </div>
            <div className="row margin-top50">
              <div className="col-sm-3 col-6 margin-btm30 black-container">
                <label className="label-text margin-rgt20" style={{fontWeight:700}}>
                Exchange rate
                  <div className="active-session-box">
                    <label className="lineheight33">INR 5</label>
                  </div>
                </label>
              </div>
              <div className="col-sm-3 col-6 margin-btm30 black-container">
                <label className="label-text margin-rgt20" style={{fontWeight:700}}>
                Tarrif rate
                  <div className="active-session-box">
                    <label className="lineheight33">1%</label>
                  </div>
                </label>
              </div>
              <div className="col-sm-3 col-6 margin-btm30 black-container">
                <label className="label-text margin-rgt20" style={{fontWeight:700}}>
                Transaction Cost
                  <div className="active-session-box">
                    <label className="lineheight33">15%</label>
                  </div>
                </label>
              </div>
            </div>
            <div className="row" style={{marginTop:10}}>
              <div className="col-xs-12 col-sm-6">
                <p className="font15 fontbold " style={{}}>Recharge Now</p>
                <div className="card card-2" style={{padding:10}}>
                  <div className="card-body">
                    <div className="row">
                      <div className="col-sm-12">
                        <div className="form-group">
                            <label className="label-text" style={{fontSize:'12pt'}}>Enter number of hours</label>
                            <input type="text" name="hours" className="form-control md-input input-text" onChange={this.onHoursChange}/>
                        </div>
                      </div>
                    </div>
                    <div className="row" style={{paddingTop:25}}>
                      <div className="col-sm-12">
                        <div className="form-group">
                            <label className="label-text" style={{fontSize:'12pt'}}>Currency Payable (with 18% GST)</label>
                            <input type="text" name="currency" className="form-control md-input input-text" defaultValue={this.state.amount} onChange={this.onAmountChange} readOnly/>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div className="col-sm-6">
                <p className="recharge_text" style={{}}><a className="navbar-links color-black" id="pay" onClick={this.pay}>Redirect to payment gateway</a></p>
              </div>
            </div>
            <div className="row" style={{marginTop:50}}>
              <div className="col-sm-12" >
              {this.state.listShow ? (
                <div>
                <table className="table table-box" style={{borderCollapse: 'separate', borderSpacing: '0 5px'}}>
                <thead style={{lineHeight:1}}>
                <tr>
                  <th className="table-padding bor-btm0 bor-top0" >Date</th>
                  <th className="table-padding bor-btm0 bor-top0" >Time Added</th>
                </tr>
                </thead>
                <tbody>
                {this.state.recharges.map(obj => (
                  <tr className="table-card" style={{cursor:'pointer'}}  key={obj.id} onClick={this.viewInfo}>
                    <td className="table-padding">{obj.date}</td>
                    <td className="table-padding">{obj.time_added}</td>
                  </tr>
                ))}
                </tbody>
                </table>
                </div>
              ) : (
                <div>
                <table className="table table-box" style={{borderCollapse: 'separate', borderSpacing: '0 5px'}}>
                <thead style={{lineHeight:1}}>
                <tr>
                  <th className="table-padding bor-btm0 bor-top0" >Date</th>
                  <th className="table-padding bor-btm0 bor-top0" >Exchange Reference Id</th>
                  <th className="table-padding bor-btm0 bor-top0" >Status</th>
                  <th className="table-padding bor-btm0 bor-top0" >Currency</th>
                  <th className="table-padding bor-btm0 bor-top0" >Amount</th>
                  <th className="table-padding bor-btm0 bor-top0" >Time Added</th>
                </tr>
                </thead>
                <tbody>
                {this.state.recharges.map(obj => (
                  <tr className="table-card" style={{cursor:'pointer'}}  key={obj.id} onClick={this.viewInfo}>
                    <td className="table-padding">{obj.date}</td>
                    <td className="table-padding">{obj.ref_id}</td>
                    <td className="table-padding">{obj.status}</td>
                    <td className="table-padding">{obj.currency}</td>
                    <td className="table-padding">{obj.amount}</td>
                    <td className="table-padding">{obj.time_added}</td>
                  </tr>
                ))}
                </tbody>
                </table>
                </div>
              )}
              </div>
            </div>
          </div>
          <Sidebar />
        </div>
        <SidebarClose />
        <div>
        <div className="modal" id="myModal">
        <div className="modal-dialog panel-box" style={{maxWidth:'100%'}}>
          <div className="modal-content">
            {/* Modal Header */}
            <div className="modal-header">
              <h4 className="modal-title">Order Number : <span id="order_number"></span></h4>
              <button type="button" className="close" data-dismiss="modal" onClick={this.close}></button>
            </div>
            {/* Modal body */}
            <div className="modal-body" style={{textAlign:'center',height:300}}>
              <img src="../assets/image/tick_green.png" alt="logo" height={100} width={100} />
              <h2 style={{fontWeight:'bold'}}>Recharge Success ! </h2>
              <h4>Your Account has been recharged by {this.state.hours} Hours.</h4>
              <h5>Great ! You will receive a confirmation message in a few minutes</h5>
            </div>
          </div>
        </div>
      </div>
        </div>
      </div>
    );
}
}
export default Recharge;



