import * as React from 'react';
import Header from '../header';
import Alert from 'react-s-alert';
import HomeSidebar from '../homeSidebar';
import HomeSidebarClose from '../homeSidebarClose';
import 'react-s-alert/dist/s-alert-default.css';
import 'react-s-alert/dist/s-alert-css-effects/slide.css';
class UserRegistration extends React.Component<MyState,MyProps> {

  constructor(props){
     super(props);
     this.register = this.register.bind(this);
   }

   register(){
     if(this.refs.pin.value===this.refs.confirmPin.value){
       fetch('http://deltabee.com/directory-service/user', {
                 method: 'POST',
                 headers : {
                     'Content-Type': 'application/json'
                   },
                 body:JSON.stringify({email:this.refs.email.value,phone:this.refs.mobileNumber.value,pin:this.refs.confirmPin.value,isActive:0,isDeleted:0})
             }).then((res) => {
               if(res.status===201){
                 this.props.history.push('/user-login');
               }
             })
             .then((data) =>  console.log(data))
             .catch((err)=>console.log(err))
     }else{
       Alert.warning('Password Not Matched. Please Enter same password', {
        position: 'top-right'
      });
     }
   }
render() {
return (
  <div>
  <Header />
  <Alert stack={true} timeout={3000} />
         <div className="container mar-top50" >
         <div id="content">
         <div className="row margin-top50">
           <div className="col-sm-12">
             <label style={{fontSize: '20pt',textTransform: 'uppercase'}}>Register As a User</label>
           </div>
         </div>
           <div className="row margin-btm25  margin-top50">
           <div className="col-sm-3 col-offset-3"></div>
             <div className="col-xs-6 col-sm-6">
               <p className="label-text fontbold" style={{fontWeight:'bold'}}>User Sign Up</p>
               <div className="card card-2">
                 <div className="card-body">
                   <div className="row margin-btm25">
                    <div className="col-sm-12">
                      <div className="form-group">
                          <label className='label-text' style={{fontSize:'12pt'}}>E-mail Id</label>
                          <input type="email" name="email" className="form-control md-input input-text" ref="email" />
                      </div>
                    </div>
                   </div>
                   <div className="row margin-btm25">
                    <div className="col-sm-12">
                      <div className="form-group">
                          <label className='label-text' style={{fontSize:'12pt'}}>Mobile Number</label>
                          <input type="text" name="mobile_number" className="form-control md-input input-text" ref="mobileNumber" />
                      </div>
                    </div>
                   </div>
                   <div className="row margin-btm25">
                    <div className="col-sm-12">
                      <div className="form-group">
                          <label className='label-text' style={{fontSize:'12pt'}}>Pin</label>
                          <input type="password" name="password" className="form-control md-input input-text" ref="pin" />
                      </div>
                    </div>
                   </div>
                   <div className="row margin-btm25">
                    <div className="col-sm-12">
                      <div className="form-group">
                          <label className='label-text' style={{fontSize:'12pt'}}>Re-Enter Pin</label>
                          <input type="password" name="confirm_password" className="form-control md-input input-text" ref="confirmPin" />
                      </div>
                    </div>
                   </div>
                 </div>
               </div>
               <div className="margin-top20">
                 <p className="text-center"><a className="navbar-links color-black" onClick={this.register}>COMPLETE REGISTRATION</a></p>
               </div>
             </div>
           </div>
         </div>
         <HomeSidebar />
         </div>
         <HomeSidebarClose />
       </div>
    );
}
}
export default UserRegistration;



