import * as React from 'react';
import Header from '../header';
import HomeSidebar from '../homeSidebar';
import HomeSidebarClose from '../homeSidebarClose';
import * as $ from 'jquery';

interface MyState {popularTutorials:any,popularVideos:any};

class Dashboard extends React.Component<MyState,MyProps> {
  constructor(props){
     super(props);
     this.state = {popularTutorials:[],popularVideos:[]};
     console.log($(window).width());
     if($(window).width()>768){
       console.log('True');
       $('#navbarSupportedContent').addClass('w-100');
     }

     fetch('http://deltabee.com/exhibit-service/exhibit?page=1', {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': sessionStorage.getItem('token'),
        }
      }).then((resp)=>{
        return resp.json()}).then((json)=>{
          this.setState({exhibits:json});
        }).catch(err => err);

     // fetch('https://api.myjson.com/bins/ze6kc', {
     //    method: 'GET',
     //    headers: {
     //      'Content-Type': 'application/json',
     //      'Authorization': sessionStorage.getItem('token'),
     //    }
     //  }).then((resp)=>{
     //    return resp.json()}).then((json)=>{
     //      this.setState({popularTutorials:json});
     //    }).catch(err => err);
     //
     //    fetch('https://api.myjson.com/bins/g1lek', {
     //       method: 'GET',
     //       headers: {
     //         'Content-Type': 'application/json',
     //         'Authorization': sessionStorage.getItem('token'),
     //       }
     //     }).then((resp)=>{
     //       return resp.json()}).then((json)=>{
     //         this.setState({popularVideos:json});
     //       }).catch(err => err);
  }

render() {
return (
  <div>
        <Header />

        <div className="container popular-content">
        <div id="content">
          <div className="row margin-top25">
            <div className="col-sm-12">
              <label className="label-text">Popular in tutorials</label>
            </div>
            {this.state.popularTutorials.map(obj => (
              <div className="col-sm-4" key={obj.id}>
              <a href="/add-scheduled-session">
                <div className="card card-box text-white bg-warning mb-3" style={{background: '#d78407!important'}}>
                  <div className="card-body">
                    <div>
                      <p className="card-text content-header" >{obj.vendor}</p>
                      <label className="exhibit-title">{obj.title}</label>
                      <label className="exhibit-count">{obj.multiplier}x</label>
                    </div>
                  </div>
                </div>
                </a>
              </div>
            ))}
          </div>
          <div className="row margin-top25">
            <div className="col-sm-12">
              <label className="label-text">Popular in video content</label>
            </div>
            {this.state.popularVideos.map(obj => (
              <div className="col-sm-4" key={obj.id}>
                <a href="/add-scheduled-session">
                  <div className="card card-box text-white bg-warning mb-3" style={{backgroundImage: 'url('+obj.backgroundImage+')', backgroundRepeat: 'no-repeat', backgroundSize: 'cover', backgroundPosition:'center'}}>
                    <div className="card-body">
                      <div>
                        <p className="card-text content-header" >{obj.vendor}</p>
                        <label className="exhibit-title">{obj.title}</label>
                        <label className="exhibit-count">{obj.multiplier}x</label>
                      </div>
                    </div>
                  </div>
                </a>
              </div>
            ))}
          </div>
        </div>
        <HomeSidebar />
        </div>
      <HomeSidebarClose />
      </div>
    );
}
}
export default Dashboard;



